@import '../../../../helpers/variables.module.scss';
@import '../../../../helpers/buttons.module.scss';

.modalAdds_page {
  text-align: center;
  background: $lightColor;
}

.modalAdds_page__tittle {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 33px;
  letter-spacing: 0.04em;
  margin: 0px 0px 20px;
}

.modalAdds_page__input {
  display: block;
  width: 100%;
  padding: 14px 11px 12px;
  background: $background;
  border: 1px solid rgba(245, 146, 86, 0.5);
  border-radius: 40px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.04em;
  color: rgba(27, 27, 27, 0.6);
  margin-top: 8px;
}

.modalAdds_page__label {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  color: #111111;
}

.modalAdds_page__button {
  display: inline-block;
  margin-top: 40px;
}

.addspet__imgInput {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.addspet__imgLabel {
  background: $background;
  background-image: url(../../../../images/add-pet.png);
  background-repeat: no-repeat;
  background-position: center;
  margin-top: 28px;
  margin-right: 160px;
  border-radius: 20px;
  display: inline-block;
  width: 116px;
  height: 116px;

  @media screen and (min-width: 768px) {
    margin-right: 350px;
    margin-top: 53px;
    width: 120px;
    height: 120px;
  }

  &:hover {
    cursor: pointer;
    background-color: rgba(245, 146, 86, 0.5);
  }
}

.modalAdds_page__input_price{
  display: flex;
  margin-top: 8px;
}

.modalAdds_page__input_select{
  display: block;
  padding: 14px 8px;
  background: $background;
  border: 1px solid rgba(245, 146, 86, 0.5);
  border-radius: 40px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.04em;
  color: rgba(27, 27, 27, 0.6);
  outline: none;
  text-align: center;
    &:last-child{
      margin-left: 10px;
      width: 85%;
      text-align: start;
      padding: 14px 11px 12px;
    }
}

.add__pet__container {
  position: relative;
}

.addspetPhoto__container {
  margin-top: 25px;
  width: 116px;
  height: 116px;
  border-radius: 40px;
  position: absolute;
  top: 2%;
  right: 50%;

  @media screen and (min-width: 768px) {
    width: 140px;
    height: 140px;
    top: 8%;
    right: 66%;
  }
}

.addspet__photo {
  display: block;
  width: 100%;
  height: auto;
}

.addspet__imgLabel_select {
  background: $background;
  border-radius: 20px;
  display: inline-block;
  min-width: 208px;
  min-height: 208px;

  &:hover {
    cursor: pointer;
    background-color: rgba(245, 146, 86, 0.5);
  }
}

.modalAdds_page__field {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  color: #111111;
  margin-bottom: 8px;
}

.modal_notice__close {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
}

.modalAdds_page_box {
  display: block;
  text-align: left;
  width: 100%;
  margin: 16px auto 0 0;
}

.modalAdds_page__photoBox {
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 20px;
}

.modalAdds_commit_box {
  display: block;
  text-align: left;
  width: 100%;
  margin-top: 20px;
}

.modalAdds_commit {
  display: inline-block;
  width: 100%;
  min-height: 115px;
  background: $background;
  border: 1px solid $accentColor;
  border-radius: 20px;
  font-family: 'Manrope';
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.04em;
  color: rgba(17, 17, 17, 0.6);
  padding-left: 14px;
  padding-top: 12px;
  margin-top: 8px;
  resize: none;
}
@media screen and (max-width: 767px) {
  .modalAdds_page__button {
    width: 100%;

    &:last-child {
      margin-top: 12px;
    }
  }
}

@media screen and (min-width: 768px) {
  .modalAdds_page__tittle {
    font-size: 36px;
    line-height: 49px;
    margin: 0px 0px 40px;
  }

  .modalAdds_page__input {
    padding: 11px 16px 10px;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.04em;
    margin-top: 12px;
  }

  .modalAdds_page__label {
    font-size: 24px;
    line-height: 26px;
  }

  .modalAdds_page__button {
    min-width: 180px;
    margin-left: 20px;

    &:last-child {
      margin-left: 0px;
    }
  }

  .addPet__button {
    display: flex;
    justify-content: center;
    flex-direction: row-reverse;
  }

  .modalAdds_commit_box {
    margin-top: 40px;
  }
}

.modalAdds_descriptions {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: #111111;
}

.star {
  color: #f59256;
}

.divNoticesCategoriesNav {
  @media screen and (max-width: 767px) {
    display: flex;
    width: 400px;
  }
}

.radioButtonSection {
  display: flex;
}

input[type="radio"] {
	display: none;
}
// .radioButtonInput {
//   -webkit-appearance: none;
//   -moz-appearance: none;
//   appearance: none;
// }

.buttonCont {
  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;
  margin-bottom: 30px;
  gap: 12px;

  @media screen and (min-width: 768px) {
    width: 290px;
  }
}

.buttonCategory {
  display: block;
  padding: 7px 24px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.36;
  letter-spacing: 0.04em;
  color: inherit;
  background-color: $lightColor;
  border: 2px solid $accentColor;
  border-radius: 40px;
  cursor: pointer;

  &:hover,
  &:focus{
    background-color: $accentColor;
  }
}

.radioButtonInput{
	&:checked {
		& + label {
			background-color: $accentColor;
		}
	}
}


.radioButton {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 40px;
  margin-bottom: 32px;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;

  &:hover,
  &:focus {
    color: $accentColor;
  }
}

.radioButtonInputSex{
	&:checked {
		& + label {
		color: $accentColor;
		}
	}
}
